import React from 'react';
import { Chart, registerables } from 'chart.js';
import { Line } from 'react-chartjs-2';

Chart.register(...registerables);

// 
interface WeightEntry {
  date: string;
  weight: number;
}

// 
interface CalorieEntry {
  date: string;
  calories: number;
}

// 
interface GraphProps {
  data: (WeightEntry | CalorieEntry)[]; // 
}

// 
const Graph: React.FC<GraphProps> = ({ data }) => {
  // 
  const dates = data.map((entry) => entry.date);

  // 
  const weights = data
    .filter((entry) => 'weight' in entry)
    .map((entry) => (entry as WeightEntry).weight);

  // 
  const calories = data
    .filter((entry) => 'calories' in entry)
    .map((entry) => (entry as CalorieEntry).calories);

  // Chart.js
  const chartData = {
    labels: dates,
    datasets: [
      {
        label: 'Weight', // 
        data: weights,
        borderColor: '#ee817b',
        backgroundColor: 'rgba(255, 255, 255, 1)',
      },
      {
        label: 'Calories', // 
        data: calories,
        borderColor: '#3e95cd',
        backgroundColor: 'rgba(255, 255, 255, 1)',
      },
    ],
  };

  // Chart.js
  const options = {
    maintainAspectRatio: false,
    responsive: false,
    plugins: {
      legend: {
        display: false,
      },
    },
  };

  return <Line height={230} width={280} data={chartData} options={options} />;
};

export default Graph;
