import { Link } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer';
import styles from './styles/Home.module.scss';
import news1 from './assets/img/news1.png';
import news2 from './assets/img/news2.png';
import news3 from './assets/img/news3.png';
import { useEffect, useState } from 'react';
import Popup from './Popup';

function Home() {
  const [hiitClicked, setHiitClicked] = useState(true);
  const [lowStrengthClicked, setLowStrengthClicked] = useState(false);
  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [selectedCourse, setSelectedCourse] = useState('');
  const [selectedLevel, setSelectedLevel] = useState('');

  useEffect(() => {
    // localStorage  selectedLevel 
    const level = localStorage.getItem('selectedLevel');
    if (level) {
      setSelectedLevel(level);
    }
  }, []); // 

  useEffect(() => {
    const course = localStorage.getItem('selectedCourse');
    if (course) {
      setSelectedCourse(course);
    }
  }, []);

  const handleHiitClick = () => {
    setHiitClicked(true);
    setLowStrengthClicked(false);
    setIsPopupOpen(true);
  };

  const handleLowStrengthClick = () => {
    setHiitClicked(false);
    setLowStrengthClicked(true);
    setIsPopupOpen(true);
  };

  const handleClosePopup = () => {
    setIsPopupOpen(false);
  };

  const randomNum = Math.floor(Math.random() * 32);

  const randomNum2 = Math.floor(Math.random() * 1200) + randomNum;

  // 030

  const linkTo = hiitClicked ? '/warmingup' : '/training';

  return (
    <div className={styles['userinfo1']}>
      <Header name='' backPath='/explainhiit' icons={true} />
      <div className={styles['margin-area']}>
        <p className={styles['hello-message']}></p>
        <div className={styles['news-wrapper']}>
          <a
            href='https://www.nikkei.com/article/DGXMZO11481080Q7A110C1000000/'
            target='_blank'
            rel='noreferrer'
          >
            <img src={news1} alt='news1' />
            <p></p>
          </a>
          <a
            href='https://www.nikkei.com/article/DGXMZO11481080Q7A110C1000000/'
            target='_blank'
            rel='noreferrer'
          >
            <img src={news2} alt='news2' />
            <p></p>
          </a>
          <a
            href='https://www.nikkei.com/article/DGXMZO11481080Q7A110C1000000/'
            target='_blank'
            rel='noreferrer'
          >
            <img src={news3} alt='news2' />
            <p></p>
          </a>
        </div>
        <p>2</p>
        <div className={styles['active-info']}>
          <div>
            <p></p>
            <span>{randomNum2}</span>
          </div>
          <div>
            <p>/</p>
            <span>{randomNum}</span>
          </div>
        </div>
        <p></p>
        <div className={styles['active-setting1']}>
          <div>
            <p></p>
            <span></span>
            <Link
              to='/typesetting'
              className={`${styles.button} ${styles['-secondary']}`}
            >
              
            </Link>
          </div>
          <div>
            <p></p>
            <span>
              {selectedCourse} Lv{selectedLevel || 4}
            </span>
            <Link
              to='/levelsetting'
              className={`${styles.button} ${styles['-secondary']}`}
            >
              
            </Link>
          </div>
        </div>
        <div className={styles['active-setting2']}>
          <p></p>
          <div>
            <div
              className={`${styles.setbutton} ${
                hiitClicked ? styles['clicked'] : ''
              }`}
              onClick={handleHiitClick}
            >
              <p>HIIT</p>
              <span></span>
            </div>
            <div
              className={`${styles.setbutton} ${
                lowStrengthClicked ? styles['clicked'] : ''
              }`}
              onClick={handleLowStrengthClick}
            >
              <p></p>
              <span></span>
            </div>
          </div>
        </div>
        <header className={styles['userinfo-header']}>
          <Link
            to={linkTo}
            className={`${styles.button} ${styles['-primary']}`}
          >
            
          </Link>
        </header>
      </div>
      <Footer />
      {isPopupOpen && (
        <Popup
          type={hiitClicked ? 'HIIT' : ''}
          onClose={handleClosePopup}
        />
      )}
    </div>
  );
}

export default Home;
